<template>
  <div id="activity">
    <!-- <a href="/internal.html/tools/provider/activity/create" target="_blank">跳转测试</a> -->
    <p class="buttonZone">
      <!-- <a
      href="/internal.html/tools/provider/activity/create" target="_blank"
       @click.prevent="this.$router.push('/internal.html/tools/provider/activity/create');">
        <div class="uploadImg  "  @click="aa">
          <img
            src="../../../assets/image/providerTools/addNewActivity.png"
            alt=""
            style="width: 50px"
          />
          <div style="margin-top: 5px; font-size: 18px; color: gray">
            添加活动
          </div>
        </div>
      </a> -->
      <a href="javascript;" @click.prevent="letAgreementShow">
        <div class="uploadImg">
          <img
            src="../../../assets/image/providerTools/addNewActivity.png"
            alt=""
            style="width: 50px"
          />
          <div style="margin-top: 5px; font-size: 18px; color: gray">
            添加商品
          </div>
        </div>
      </a>
    </p>

    <Scroll class="content2" ref="scroll" :probeType="3">
      <activity-list
        v-show="activityList.length !== 0"
        :list="activityList"
        @imageUploadInScroll="refershScrollComponent"
        @submitLauchProductRequestTicket="showEditReasonDialogAndRecordIndex"
      ></activity-list>
      <empty-data-placeholder
        v-show="activityList.length === 0"
        description="暂无商品数据"
      ></empty-data-placeholder>
    </Scroll>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>
    <div class="edit_reason-dialog-zone" v-show="ifShowEditReasonDialog">
      <edit-reason-dialog
        @ensureClick="submitLauchProductRequestTicket"
        @cancelClick="hideEditReasonDialogAndOtherProcess"
        ref="editReasonDialog"
      ></edit-reason-dialog>
    </div>
    <confirm-message
      v-if="isShowAgreement"
      @closeBox="letAgreementClose"
    ></confirm-message>
  </div>
</template>

<script>
import Scroll from "../../../components/common/scroll/Scroll.vue";
import ActivityList from "./childComponents/activityList/ActivityList.vue";
import EditReasonDialog from "../../../components/content/providerTools/editReasonDialog/EditReasonDialog.vue";
import EmptyDataPlaceholder from "../../../components/common/emptyDataPlaceholder/EmptyDataPlaceholder.vue";
import ConfirmMessage from "../../providerTools/activityCreateForm/components/ConfirmMessage.vue";
import {
  $getActivityList,
  $getProductByID,
  $submitLaunchProductRequestTicket,
} from "../../../network/providerTools/activity";

export default {
  name: "e-Activity",
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isProvider") === "true") {
      window.localStorage.setItem("currentIndex010006", 2);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010006");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
    }
  },
  async created() {
    console.log("created");
    // console.log('activitated-1',this)
    this.isLoading = true;
    console.log(this);
    const userid = window.localStorage.getItem("userID");
    const res = await $getActivityList({ user_id: userid });
    console.log(res);
    if (res.Code === 200 && res.msg === "success") {
      this.isLoading = false;
      let list = res.details.products;
      list = list
        ? list.map((item) => {
            if (item.categories instanceof Array) {
              item.categories = item.categories.map((val) => {
                if (Object.prototype.toString.call(val) == "[object Object]") {
                  val = val.id;
                }
                return val;
              });
              return item;
            }
            item.categories = [];
            return item;
          })
        : [];

      res.details.products = list;
      this.activityList = window.JSON.parse(
        window.JSON.stringify(res.details.products)
      );
      if (this.activityList.length === 0) {
        this.$toast.success("产品列表为空", 240);
      } else {
        this.$toast.success("获取产品列表成功", 240);
      }
    } else {
      this.$toast.error("获取产品列表失败", 240);
    }
    this.isLoading = false;
  },
  async activated() {
    // console.log('activitated-1',this)
    this.isLoading = true;
    const userid = window.localStorage.getItem("userID");
    const res = await $getActivityList({ user_id: userid });
    console.log("activityres", res);
    if (res.Code === 200 && res.msg === "success") {
      this.isLoading = false;
      this.activityList = window.JSON.parse(
        window.JSON.stringify(res.details.products)
      );
      if (this.activityList.length === 0) {
        this.$toast.success("产品列表为空", 240);
      } else {
        this.$toast.success("获取产品列表成功", 240);
      }
    } else {
      this.$toast.error("获取产品列表失败", 240);
    }
    this.isLoading = false;
  },
  data() {
    return {
      isLoading: false,
      activityList: [],
      ifShowEditReasonDialog: false,
      activeIndex: -1,
      viewBoardVisible: false,
      isShowAgreement: false,
    };
  },
  methods: {
    letAgreementShow() {
      this.isShowAgreement = true;
    },
    addNew() {
      this.$store.commit("productPage/setStatus", 0);
      this.$router.push({ name: "ProviderActivityCreateForm" }).then(() => {
        if (window.isCopy) {
          let state = window.localStorage.getItem("state");
          state = JSON.parse(state);
          this.$store.commit("productPage/loadCache", state);
          window.isCopy = false;

        } else {
        }
      });
    },
    letAgreementClose(isConfirm) {
      this.isShowAgreement = false;
      if (isConfirm) {
        this.addNew();
      } else {
        window.isCopy = false;
        return;
      }
    },
    aa() {
      // location.href="http://www.baidu.com"
      window.open("/internal.html/tools/provider/activity/create");
      // window.open("http://www.baidu.com",'_blank','width:200px height:200px top:20px left:20px')
    },
    refershScrollComponent() {
      this.$refs.scroll.refresh();
    },
    showEditReasonDialogAndRecordIndex(index) {
      this.$refs.editReasonDialog.$data.placeholder = `product_id=${this.activityList[index].id}正在申请上架，请输入申请理由或申请说明`;
      this.ifShowEditReasonDialog = true;
      this.activeIndex = index;
    },
    hideEditReasonDialogAndOtherProcess() {
      this.$refs.editReasonDialog.placeholder = "";
      this.ifShowEditReasonDialog = false;
      this.activeIndex = -1;
    },
    async submitLauchProductRequestTicket(message) {
      if (message.trim() === "") {
        this.$toast.error("申请理由不能为空", 240);
        return;
      }

      const productId = this.activityList[this.activeIndex].id;
      const ticketContent = {
        id: productId,
      };
      this.$refs.editReasonDialog.ifShowLoadingGif = true;
      try {
        const response = await $submitLaunchProductRequestTicket(
          message.trim(),
          ticketContent
        );
        if (response.Code === 200 && response.msg === "success") {
          this.$toast.success("提交成功，请等待审核", 240, 330);
          // console.log(this.$toast)
        } else {
          this.$toast.error("提交失败", 240);
        }
        this.$refs.editReasonDialog.placeholder = "";
        this.ifShowEditReasonDialog = false;
        this.activeIndex = -1;
      } catch (err) {
        this.$toast.error("提交失败", 240);
        this.$refs.editReasonDialog.placeholder = "";
        this.ifShowEditReasonDialog = false;
        this.activeIndex = -1;
      }
      this.$refs.editReasonDialog.ifShowLoadingGif = false;
    },
    hideViewDialog() {
      this.viewBoardVisible = false;
    },
  },
  components: {
    Scroll,
    ActivityList,
    EditReasonDialog,
    EmptyDataPlaceholder,
    ConfirmMessage,
  },
  mounted() {
    if (window.isCopy) this.letAgreementShow();
  },
};
</script>

