<script>
export default {
    mounted(){
   let time =setInterval(()=>{
        if(this.timer>0){
            this.timer--
        }else{
            clearInterval(time)
        }
    },1000)
    },
    computed:{
        getTimer(){
            if(this.timer!==0){
                 return `(${this.timer}s)`
            }
            return ''
        },
        actived(){
            return this.timer===0
        }
    },
    data(){
        return{
            timer:5
        }
    },
    methods:{
        confirm(){
           if(this.timer!==0){return}
            this.$emit('closeBox',true)
        },
        close(){
            this.$emit('closeBox',false)
        }
    }
}
</script>

