<template>
  <div id="activity-list">
    <activity-list-item
      v-for="(item, index) in list"
      :key="item.id"
      class="activity-list-item"
    >
      <div class="imageZone">
        <div
          style="background: rgba(0, 0, 0); border-radius: 12px; width: 100%"
        >
          <img
            v-lazy="{
              src: getActivityThumbnail(item.horizontal_shows),
              loading: require('../../../../../../../assets/placeholder_horizontal.png'),
            }"
            @load="activityThumbUpload"
            style="opacity: 0.8; cursor: pointer"
            @click="listItemClick(item.id, item.status)"
          />
          <!--           <div class="replicate" v-if="item.status==5400" @click="replicate(item)">复制产品</div>-->
        </div>

        <div class="error">
          <img
            src="../../../../../assets/image/providerTools/categoryManagement/error.svg"
            @click="deleteImgClick(index)"
          />
        </div>
        <div
          class="status"
          style="cursor: pointer"
          @click="listItemClick(item.id, item.status)"
        >
          {{ showStatusStr(item.status) }}
        </div>

        <!--        <div class="status" v-show="item.status === 5900">-->
        <!--          <button-->
        <!--            class="statusBtn"-->
        <!--            @click="submitLauchProductRequestTicket(index)"-->
        <!--          >-->
        <!--            {{ showStatusStr(item.status) }}-->
        <!--          </button>-->
        <!--        </div>-->
      </div>
      <div class="basicInfoZone">
        <p class="firstLine">
          <span
            >{{ item.theme_related }} · {{ item.age_min }}岁-{{
              item.age_max
            }}岁 · {{ item.activity_days }}天</span
          >
        </p>
        <p class="name">{{ item.short_name }}</p>
        <div class="lastLine">
          <p>￥{{ getReallyPrice(item.price_selling) }}</p>
          <!-- <p>￥{{item.price_selling}}</p> -->
          <p>
            {{ processDate(item.activity_start_time) }}-{{
              processDate(item.activity_end_time)
            }}
          </p>
          <p>{{ item.location_city }}{{ item.location_district }}</p>
        </div>
      </div>
    </activity-list-item>
  </div>
</template>

<script>
/* eslint-disable */
import { onMounted } from "@vue/runtime-core";
import ActivityListItem from "./ActivityListItem.vue";
export default {
  name: "e-ActivityList",
  props: {
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {};
  },
  mounted() {
    console.log(this.list);
  },
  methods: {
    getReallyPrice(val) {
      return Number(val) / 100;
    },
    replicate(item) {
      console.log(item);
      let info = { ...item };
      console.log(info);
      console.log(window.localStorage);
      window.localStorage.setItem("newAct", JSON.stringify(info));
      console.log("localstorage completed");
      this.$toast.success("复制成功", 200, 400, 3000);
      window.open("/internal.html/tools/provider/activity/create");
    },
    processDate(dateStr) {
      const dateStr2 = dateStr.replace("T", "-");
      const arr = dateStr2.split("-");
      // console.log(arr);
      return `${arr[1]}/${arr[2]}`;
    },
    listItemClick(id, statusCode) {
      console.log("click: ");
      console.log(id, statusCode);
      // return
      if (
        [4300, 5100, 5200, 5300, 5310, 5400, 5900, 5700].indexOf(statusCode) !=
        -1
      ) {
        this.$store.commit("productPage/setStatus", Number(statusCode));
        this.$store.commit("productPage/setId", Number(id));
        this.$router.push(`/tools/provider/activity/productDetail/${id}`);
      } else {
        console.log("预留");
      }
      /* this.$toast.error('这部分还要修改，先阻止你，不好意思', 240, 370); */
    },
    showStatusStr(statusCode) {
      switch (statusCode) {
        case 4300:
          return "审核不通过";
          break;
        case 5100:
          return "草稿商品";
          break;
        case 5200:
          return "待保险公司核保";
          break;
        case 5300:
          return "待提交上架";
          break;
        case 5310:
          return "已提交上架待审核";
          break;
        case 5400:
          return "已上架";
          break;
        case 5900:
          return "点击提交上架请求";
          break;
        default:
          return "默认状态";
      }
    },
    getActivityThumbnail(horizontalPictureList) {
      if (horizontalPictureList.length !== 0) {
        const arr = Object.entries(horizontalPictureList[0]);
        return arr[0][1];
      } else {
        return require("../../../../../../../assets/placeholder_horizontal.png");
      }
    },
    activityThumbUpload() {
      this.$emit("imageUploadInScroll");
    },
    deleteImgClick(index) {
      console.log("123");
    },
    submitLauchProductRequestTicket(index) {
      this.$emit("submitLauchProductRequestTicket", index);
    },
  },
  components: {
    ActivityListItem,
  },
  created() {
    console.log("activity list created", this.list);
  },
};
</script>

